@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loader-container {
  position: relative;
  background-color: rgba(55, 55, 55, 0.5);
}

.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
  width: 2em;
  height: 2em;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation: load 1.8s infinite ease-in-out;
          animation: load 1.8s infinite ease-in-out;
}

.loader {
  position: absolute;
  margin-top: 50%;
  left: 50%;
  color: #1db954;
  font-size: 10px;
  -webkit-animation-delay: -0.16s;
          animation-delay: -0.16s;
}

.loader:before,
.loader:after {
  content: '';
  position: absolute;
  top: 0;
}

.loader:before {
  left: -3em;
  -webkit-animation-delay: -0.32s;
          animation-delay: -0.32s;
}

.loader:after {
  left: 3em;
}

@-webkit-keyframes load {
  0%,
  80%,
  100% {
    box-shadow: 0 2em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2em 0 0;
  }
}

@keyframes load {
  0%,
  80%,
  100% {
    box-shadow: 0 2em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2em 0 0;
  }
}

.App {
  text-align: left;
  color: #f9f9f9;
  min-height: calc(100% - 20px);
  min-width: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  font-family: 'Montserrat', sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, from(#373737), to(#181818));
  background: -webkit-linear-gradient(top, #373737, #181818);
  background: linear-gradient(180deg, #373737, #181818);
  padding-bottom: 20px;
}

#header {
  padding: 15px 25px;
  display: flex;
}

#header .app-title {
  color: #1db954;
  font-size: 32px;
  font-weight: bold;
}

#header .show-filters {
  height: 25px;
  margin-top: 10px;
  margin-left: 30px;
  padding: 0 15px;
  background-color: transparent;
  border: 1px solid #fff;
  border-radius: 50vh;
  color: #fff;
  font-size: 11px;
  font-family: 'Montserrat', sans-serif;
  outline: none;
  display: flex;
  align-items: center;
}

#header .show-filters .arrow {
  margin-top: 4px;
  margin-left: 4px;
}

#contents {
  display: flex;
  flex-direction: row;
}

.playlist {
  display: flex;
  padding: 20px 50px;
  text-decoration: none;
  color: #b3b3b3;
  font-size: 12px;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .playlist.playlist-small .info {
    display: none;
  }

  .playlist.playlist-small {
    padding: 20px;
  }
}

.playlist:hover,
.playlist.active:hover {
  background-color: rgba(255, 255, 255, 0.3);
}

.playlist.active {
  background-color: rgba(255, 255, 255, 0.2);
}

.playlist .info {
  margin: 7px 0 7px 20px;
}

.playlist .info .name {
  color: #f9f9f9;
  font-size: 16px;
  font-weight: bold;
}

.playlist .info .owner {
  color: #b3b3b3;
  font-weight: bold;
}

@media screen and (max-width: 768px) {
  .pagination .page-limit {
    display: none;
  }

  .pagination.small {
    justify-content: center;
  }

  .pagination.small .arrow {
    font-size: 20px;
  }

  .pagination.small .page-info {
    display: none;
  }
}

@media screen and (min-width: 769px) {
  .pagination .page-limit {
    display: flex;
    flex-direction: row;
  }
}

.pagination {
  margin: 8px 16px;
  text-align: end;
  display: flex;
  justify-content: flex-end;
}

.pagination .arrow {
  cursor: pointer;
  font-size: 16px;
  margin: 1px 3px;
}

.pagination .arrow.inactive {
  opacity: 0.3;
}

.pagination select,
.pagination input[type='text'] {
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 13px;
}

.pagination .page-number {
  width: 14px;
  margin: 0 5px;
}

.pagination .input {
  background-color: white;
  border-radius: 50vh;
  margin: 0 5px;
}

.pagination .page-info {
  display: flex;
  flex-direction: row;
}

.pagination.small .page-limit {
  display: none;
}

@media screen and (max-width: 480px) {
  .playlist-container.active-playlist {
    display: none;
  }
}

@media screen and (min-width: 481px) and (max-width: 768px) {
  .playlist-container.active-playlist .title {
    display: none;
  }
}

@media screen and (max-width: 769px) {
  .playlist-container {
    flex: 1 1;
  }

  .playlist-container.active-playlist {
    flex: unset;
  }
}

@media screen and (min-width: 769px) {
  .playlist-container {
    flex: 2 1;
  }
}

.playlist-container .title {
  font-size: 28px;
  font-weight: bold;
  margin: 20px 50px;
}

.playlist-container.loading {
  opacity: 0.5;
}

select {
  border: none;
  border-radius: 1px;
}

.filter {
  margin-bottom: 16px;
}

.filter-name {
  font-size: 12px;
  margin: 0 5px 5px 10px;
}

.filter-input {
  border: none;
  border-radius: 50vh;
  background-color: #ffffff;
  padding: 1px 10px;
  width: 200px;
  margin-right: 10px;
  border: 1px solid transparent;
}

.filter-input > input[type='text'],
.filter-input > input[type='number'],
.filter-input > select,
.react-datetime-picker__wrapper {
  border: none;
  background-color: transparent;
  height: 22px;
  width: 200px;
  color: #373737;
  outline: none;
}

.filter-input.error {
  border: 1px solid #e34242;
  box-shadow: 0 0 5px #e34242;
}

.filter .error-message {
  color: #e34242;
}

@media screen and (max-width: 480px) {
  .filter-panel.active {
    display: inline-table;
  }

  .filter-panel.visible {
    height: 128px;
  }
}

@media screen and (min-width: 481px) {
  .filter-panel {
    display: inline-flex;
  }

  .filter-panel.visible {
    height: 64px;
  }
}

.filter-panel {
  padding: 0 32px;
  height: 0px;
  -webkit-transition: height 0.3s;
  transition: height 0.3s;
  visibility: hidden;
}

.filter-panel.visible {
  -webkit-transition: height 0.3s;
  transition: height 0.3s;
  visibility: visible;
}

@media screen and (max-width: 480px) {
  .track-container {
    min-width: 350px;
  }
}

@media (min-width: 481px) {
  .track-container {
    min-width: 50%;
  }
}

.track-container {
  padding: 24px;
  background-color: rgba(255, 255, 255, 0.2);
  flex: 3 1;
}

.track-container .close-button {
  font-size: 32px;
  cursor: pointer;
}

.track-container .close-button:hover {
  opacity: 0.5;
}

.playlist-info {
  display: flex;
  flex-direction: row;
}

.playlist-info .playlist-details {
  margin: 24px 0 0 16px;
  flex: 1 1;
}

.playlist-info .playlist-details .list-type {
  text-transform: uppercase;
  font-size: 12px;
}

.playlist-info .playlist-details .playlist-name {
  font-weight: bold;
  font-size: 36px;
  margin-bottom: 8px;
}

.playlist-info .playlist-details .track-count {
  color: #b3b3b3;
}

.playlist-info .playlist-details .playlist-owner {
  color: #f2f2f2;
  font-weight: bold;
}

.track-list {
  margin-top: 16px;
}

.track {
  border-bottom: 1px solid #3f3f3f;
  padding: 8px 16px;
}

.track:hover {
  background-color: #3f3f3f;
}

.track .artist {
  color: #b3b3b3;
}

.track-container.loading {
  opacity: 0.5;
}

